import {
  Component,
  ComponentFactoryResolver,
  ComponentRef,
  Input,
  OnInit,
  Type,
  ViewContainerRef
} from '@angular/core';
import { DynamicFormComponentType } from './types/dynamic-form-component.type';

@Component({
  selector: 'sof-dynamic-form',
  template: ``
})
export class DynamicFormComponent<T> implements OnInit {
  /**
   * The component that must be dynamically injected into the template
   */
  @Input() dynamicFormComponent: Type<DynamicFormComponentType<T>>;
  private componentRef: ComponentRef<DynamicFormComponentType<T>>;
  private localTc: string;
  private localData: T;

  constructor(
    private componentFactoryResolver: ComponentFactoryResolver,
    private viewContainerRef: ViewContainerRef
  ) {}

  /**
   * The translation context.
   */
  @Input() set tc(tc: string) {
    this.localTc = tc;

    if (this.componentRef) {
      (this.componentRef.instance as any).tc = this.localTc;
    }
  }

  /**
   * The data we want to pass to the dynamic attribute.
   */
  @Input() set data(data: T) {
    this.localData = data;

    if (this.componentRef) {
      (this.componentRef.instance as any).data = this.localData;
    }
  }

  ngOnInit(): void {
    const factory = this.componentFactoryResolver.resolveComponentFactory(
      this.dynamicFormComponent
    );
    this.componentRef = this.viewContainerRef.createComponent(factory);
    (this.componentRef.instance as any).tc = this.localTc;
    (this.componentRef.instance as any).data = this.localData;
  }
}
